body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #111;
}

.sec7-container {
  display: flex;
  background-color: #111;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 120vh;
  margin: 10%;
}

.sec7-text {
  font-size: 10vw;
  letter-spacing: -.01em;
  line-height: 100%;
  margin: 0;
  width: 100%;
  color: rgba(173, 173, 173, 0.5);
  background: linear-gradient(to right, #003cff, #f99b9b) no-repeat;
  -webkit-background-clip: text;
  background-clip: text;
  background-size: 0%;
  transition: background-size cubic-bezier(.1,.5,.5,1) 0.5s;
  border-bottom: 1px solid #2F2B28;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.sec7-text span {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #A0D3F2;
  color: #000000;
  clip-path: polygon(0 50%, 100% 50%, 100% 50%, 0 50%);
  transform-origin: center;
  transition: all cubic-bezier(.1,.5,.5,1) 0.4s;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  top: 0;
  font-size: inherit;
  text-align: left;
  padding-left: 0.1em;
}

.sec7-text:hover > span {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
}

.sec7-text a {
  text-decoration: none;
  color: inherit;
}
