@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard-dynamic-subset.css');
@font-face {
    font-family: 'Cafe24Ohsquare';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/Cafe24Ohsquare.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body { background: #ffebcd; font-weight: 700; font-family: 'pretendard'; }

#sec1-sample {
    position: relative;
    height: 170vh;
    font-size: 4vw;
    font-weight: 900;
    background-color: #fff;
    color: #000;
}

.sec1-txt_area {
    /* pin 대상이므로 sticky 대신 pin을 사용 */
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    /* 중앙 정렬 */
}

.sec1-txt_area p {
    font-family: Cafe24Ohsquare;
    overflow: hidden;
    white-space: nowrap;
}

.sec1-txt_area p + p {
    margin-left: 10px;
}

.sec1-word01,
.sec1-word02 {
    opacity: 0;
    width: 0;
}
