@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
@font-face {
    font-family: 'SF_HambakSnow';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2106@1.1/SF_HambakSnow.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: rgba(30,30,30,0.96);
}

ul, li, dl, dt, dd, p, span {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
*, :after, :before {
  box-sizing: border-box;
}

.sec6-hor-wrapper {
  overflow: hidden;
  width: 100vw;
  height: 100vh !important;
  min-height: 100vh !important;
  position: relative;
  background: #111;
}

.sec6-hor {
  overflow: hidden;
  display: flex;
  flex-wrap: nowrap;
  width: 300vw;
  height: 100vh !important;
  min-height: 100vh !important;
}

.sec6-items {
  width: 100vw;
  height: 100vh !important;
  min-height: 100vh !important;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #111;
  overflow: hidden;
}

/* 첫 번째 섹션: 상단 이미지, 하단 텍스트 */
.sec6-half-section {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  background: #111;
  padding: 0;
}

.sec6-half-wrap {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.sec6-half-img {
  width: 100vw;
  height: 50vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.sec6-half-text {
  width: 100vw;
  height: 50vh;
  background: #111;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 8vw;
  padding-right: 4vw;
  box-sizing: border-box;
}

.sec6-half-num {
  font-size: 2.2rem;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  margin-bottom: 1.1rem;
  letter-spacing: 0.07em;
  color: #7de2ff;
}

.sec6-half-title {
  font-size: 2.1rem;
  font-weight: 700;
  margin-bottom: 1.2rem;
  font-family: 'Montserrat', 'Poppins', sans-serif;
}

.sec6-half-desc {
  font-size: 1.08rem;
  font-family: 'Pretendard', 'Poppins', sans-serif;
  color: #e0e0e0;
  line-height: 1.7;
  opacity: 0.92;
}

/* 세 번째 슬라이드(section 전체 배경 이미지) */
.sec6-bgfull-section {
  background-size: cover !important;
  background-position: center !important;
  background-repeat: no-repeat !important;
  position: relative;
  overflow: hidden;
}

/* 텍스트 오버레이 */
.sec6-bgfull-overlay {
  position: absolute;
  inset: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 2;
  pointer-events: none;
  padding: 0 7vw;
}

.sec6-bgfull-left {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  justify-content: center;
  color: #fff;
  font-family: 'Montserrat', 'Poppins', sans-serif;
  font-size: 2.3rem;
  font-weight: 1000;
  letter-spacing: 0.01em;
  text-align: left;
  line-height: 1.18;
}

.sec6-bgfull-right {
  display: flex;
  flex-direction: column;
  gap: 1.4rem;
  color: #fff;
  font-family: 'Poppins', 'Pretendard', sans-serif;
  font-size: 1.14rem;
  text-align: left;
  min-width: 320px;
}
.sec6-bgfull-right > div {
  display: flex;
  flex-direction: row;
  gap: 1.2rem;
  align-items: baseline;
  margin-bottom: 0.2rem;
}
.sec6-label {
  min-width: 4.5em;
  font-size: 1.08rem;
  color: #e0e0e0;
  font-weight: 400;
  letter-spacing: 0.01em;
}
.sec6-value {
  font-size: 1.12rem;
  color: #fff;
  font-weight: 500;
  letter-spacing: 0.01em;
  word-break: keep-all;
}

/* 기존 이미지 슬라이드 */
.sec6-img {
  width: 20%;
  height: 60%;
  border-radius: 5px;
  background-size: cover;
  background-position: center !important;
}

/* 텍스트 디자인 슬라이드 */
.sec6-img.sec6-img-design {
  width: 60vw;
  min-width: 420px;
  max-width: 900px;
  height: 60vh;
  min-height: 340px;
  background: none !important;
  border-radius: 18px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  padding: 3.5rem 3rem 3rem 3.5rem;
  flex-direction: column;
  color: #fff;
  font-family: 'SF_HambakSnow', 'Montserrat', 'Poppins', sans-serif;
  background-color: rgba(30,30,30,0.96);
}

.sec6-design-center {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.sec6-design-top {
  font-family: 'Montserrat', 'Poppins', sans-serif;
  font-size: 1.25rem;
  font-weight: 500;
  margin-bottom: 1.8rem;
  color: #7de2ff;
  letter-spacing: -1px;
}

.sec6-design-big {
  font-family: 'SF_HambakSnow', 'Montserrat', sans-serif;
  font-size: 2.8rem;
  font-weight: 700;
  line-height: 1.15;
  margin-bottom: 2.2rem;
  color: #fff;
  letter-spacing: -0.03em;
  text-shadow: 0 2px 12px rgba(0,0,0,0.18);
}

.sec6-design-en {
  font-family: 'Montserrat', 'Poppins', sans-serif;
  font-size: 1.12rem;
  color: #b3e5fc;
  font-weight: 500;
  margin-bottom: 1.3rem;
  align-self: flex-end;
  text-align: right;
  letter-spacing: -0.01em;
}

.sec6-design-ko {
  font-family: 'SF_HambakSnow', 'Poppins', sans-serif;
  font-size: 1.03rem;
  color: #f5f5f5;
  font-weight: 400;
  margin-top: 1.2rem;
  line-height: 1.7;
  align-self: flex-end;
  text-align: right;
  opacity: 0.85;
}

@media (max-width: 900px) {
  .sec6-half-text { padding-left: 3vw; padding-right: 2vw; }
  .sec6-half-num { font-size: 1.4rem; }
  .sec6-half-title { font-size: 1.2rem; }
  .sec6-half-desc { font-size: 0.95rem; }
  .sec6-bgfull-overlay {
    padding: 0 3vw;
  }
  .sec6-bgfull-left {
    font-size: 1.3rem;
    gap: 1.3rem;
  }
  .sec6-bgfull-right {
    font-size: 0.98rem;
    min-width: 160px;
    gap: 0.7rem;
  }
  .sec6-label { font-size: 0.95rem; }
  .sec6-value { font-size: 1rem; }
  .sec6-img.sec6-img-design {
    width: 90vw;
    min-width: 0;
    padding: 2rem 1.2rem 2rem 1.5rem;
  }
  .sec6-design-big { font-size: 1.6rem; }
  .sec6-design-top { font-size: 1rem; }
  .sec6-design-en { font-size: 0.95rem; }
  .sec6-design-ko { font-size: 0.9rem; }
}
@media (max-width: 600px) {
  .sec6-half-text { padding-left: 1vw; padding-right: 1vw; }
  .sec6-half-num { font-size: 1.1rem; }
  .sec6-half-title { font-size: 1.05rem; }
  .sec6-half-desc { font-size: 0.88rem; }
  .sec6-bgfull-overlay {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 4vh 2vw;
    gap: 2.5rem;
  }
  .sec6-bgfull-left, .sec6-bgfull-right {
    font-size: 1rem;
    gap: 0.7rem;
    min-width: 0;
  }
  .sec6-bgfull-right {
    margin-top: 2.5rem;
  }
  .sec6-img.sec6-img-design { padding-left: 1vw; padding-top: 2vh; }
  .sec6-design-big { font-size: 1.3rem; }
  .sec6-design-en, .sec6-design-ko { margin-right: 0; }
}
