body {
  --background-color: #111;
  --text-color: #A1A1AA;
  --card-background-color: rgba(255, 255, 255, .015);
  --card-border-color: rgba(255, 255, 255, 0.1);
  --card-box-shadow-1: rgba(0, 0, 0, 0.05);
  --card-box-shadow-1-y: 3px;
  --card-box-shadow-1-blur: 6px;
  --card-box-shadow-2: rgba(0, 0, 0, 0.1);
  --card-box-shadow-2-y: 8px;
  --card-box-shadow-2-blur: 15px;
  --card-label-color: #FFFFFF;
  --card-icon-color: #D4D4D8;
  --card-icon-background-color: rgba(255, 255, 255, 0.08);
  --card-icon-border-color: rgba(255, 255, 255, 0.12);
  --card-shine-opacity: .1;
  --card-shine-gradient: conic-gradient(from 205deg at 50% 50%, rgba(16, 185, 129, 0) 0deg, #10B981 50deg, rgba(52, 211, 153, 0.18) 295deg, rgba(16, 185, 129, 0) 360deg);
  --card-line-color: #2A2B2C;
  --card-tile-color: rgba(16, 185, 129, 0.05);
  --card-hover-border-color: rgba(255, 255, 255, 0.2);
  --card-hover-box-shadow-1: rgba(0, 0, 0, 0.04);
  --card-hover-box-shadow-1-y: 5px;
  --card-hover-box-shadow-1-blur: 10px;
  --card-hover-box-shadow-2: rgba(0, 0, 0, 0.3);
  --card-hover-box-shadow-2-y: 15px;
  --card-hover-box-shadow-2-blur: 25px;
  --card-hover-icon-color: #34D399;
  --card-hover-icon-background-color: rgba(52, 211, 153, 0.1);
  --card-hover-icon-border-color: rgba(52, 211, 153, 0.2);
  --blur-opacity: .01;
}

body.sec5-light {
  --background-color: #FAFAFA;
  --text-color: #52525B;
  --card-background-color: transparent;
  --card-border-color: rgba(24, 24, 27, 0.08);
  --card-box-shadow-1: rgba(24, 24, 27, 0.02);
  --card-box-shadow-2: rgba(24, 24, 27, 0.04);
  --card-label-color: #18181B;
  --card-icon-color: #18181B;
  --card-icon-background-color: rgba(24, 24, 27, 0.04);
  --card-icon-border-color: rgba(24, 24, 27, 0.1);
  --card-shine-opacity: .3;
  --card-shine-gradient: conic-gradient(from 225deg at 50% 50%, rgba(16, 185, 129, 0) 0deg, #10B981 25deg, #EDFAF6 285deg, #FFFFFF 345deg, rgba(16, 185, 129, 0) 360deg);
  --card-line-color: #E9E9E7;
  --card-tile-color: rgba(16, 185, 129, 0.08);
  --card-hover-border-color: rgba(24, 24, 27, 0.15);
  --card-hover-box-shadow-1: rgba(24, 24, 27, 0.05);
  --card-hover-box-shadow-2: rgba(24, 24, 27, 0.1);
  --card-hover-icon-color: #18181B;
  --card-hover-icon-background-color: rgba(24, 24, 27, 0.04);
  --card-hover-icon-border-color: rgba(24, 24, 27, 0.34);
  --blur-opacity: .1;
}


.sec5-fullpage {
  width: 100vw;
  height: 100vh;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--background-color);
  position: relative;
}

.sec5-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(240px, 1fr));
  grid-template-rows: repeat(2, minmax(240px, 1fr));
  grid-gap: 32px;
  width: 90vw;
  max-width: 900px;
  height: 80vh;
  align-items: center;
  justify-items: center;
  margin: auto;
}

.sec5-card {
  background-color: var(--background-color);
  box-shadow: 0px var(--card-box-shadow-1-y) var(--card-box-shadow-1-blur) var(--card-box-shadow-1), 0px var(--card-box-shadow-2-y) var(--card-box-shadow-2-blur) var(--card-box-shadow-2), 0 0 0 1px var(--card-border-color);
  padding: 56px 16px 16px 16px;
  border-radius: 15px;
  cursor: pointer;
  position: relative;
  transition: box-shadow .25s;
  overflow: hidden;
  width: 100%;
  height: 100%;
  min-width: 200px;
  min-height: 200px;
  max-width: 350px;
  max-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.sec5-card::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 15px;
  background-color: var(--card-background-color);
}

.sec5-icon {
  z-index: 2;
  position: relative;
  display: table;
  padding: 8px;
}
.sec5-icon::after {
  content: '';
  position: absolute;
  inset: 4.5px;
  border-radius: 50%;
  background-color: var(--card-icon-background-color);
  border: 1px solid var(--card-icon-border-color);
  backdrop-filter: blur(2px);
  transition: background-color .25s, border-color .25s;
}
.sec5-icon svg {
  position: relative;
  z-index: 1;
  display: block;
  width: 24px;
  height: 24px;
  color: var(--card-icon-color);
  transition: color .25s;
}

.sec5-card h4 {
  z-index: 2;
  position: relative;
  margin: 12px 0 4px 0;
  font-family: inherit;
  font-weight: 600;
  font-size: 16px;
  line-height: 2;
  color: var(--card-label-color);
}

.sec5-card p {
  z-index: 2;
  position: relative;
  margin: 0;
  font-size: 14px;
  line-height: 1.7;
  color: var(--text-color);
  text-align: center;
}

.sec5-shine {
  border-radius: inherit;
  position: absolute;
  inset: 0;
  z-index: 1;
  overflow: hidden;
  opacity: 0;
  transition: opacity .5s;
}
.sec5-shine:before {
  content: '';
  width: 150%;
  padding-bottom: 150%;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  bottom: 55%;
  filter: blur(35px);
  opacity: var(--card-shine-opacity);
  transform: translateX(-50%);
  background-image: var(--card-shine-gradient);
}

.sec5-background {
  border-radius: inherit;
  position: absolute;
  inset: 0;
  overflow: hidden;
  -webkit-mask-image: radial-gradient(circle at 60% 5%, black 0%, black 15%, transparent 60%);
  mask-image: radial-gradient(circle at 60% 5%, black 0%, black 15%, transparent 60%);
}

.sec5-tiles {
  opacity: 0;
  transition: opacity .25s;
}
.sec5-tile {
  position: absolute;
  background-color: var(--card-tile-color);
  opacity: 0;
}
.sec5-tile-1 { top: 0; left: 0; height: 10%; width: 22.5%; }
.sec5-tile-2 { top: 0; left: 22.5%; height: 10%; width: 27.5%; }
.sec5-tile-3 { top: 0; left: 50%; height: 10%; width: 27.5%; }
.sec5-tile-4 { top: 0; left: 77.5%; height: 10%; width: 22.5%; }
.sec5-tile-5 { top: 10%; left: 0; height: 22.5%; width: 22.5%; }
.sec5-tile-6 { top: 10%; left: 22.5%; height: 22.5%; width: 27.5%; }
.sec5-tile-7 { top: 10%; left: 50%; height: 22.5%; width: 27.5%; }
.sec5-tile-8 { top: 10%; left: 77.5%; height: 22.5%; width: 22.5%; }
.sec5-tile-9 { top: 32.5%; left: 50%; height: 22.5%; width: 27.5%; }
.sec5-tile-10 { top: 32.5%; left: 77.5%; height: 22.5%; width: 22.5%; }

.sec5-line {
  position: absolute;
  inset: 0;
  opacity: 0;
  transition: opacity .35s;
}
.sec5-line:before,
.sec5-line:after {
  content: '';
  position: absolute;
  background-color: var(--card-line-color);
  transition: transform .35s;
}
.sec5-line:before { left: 0; right: 0; height: 1px; transform-origin: 0 50%; transform: scaleX(0);}
.sec5-line:after { top: 0; bottom: 0; width: 1px; transform-origin: 50% 0; transform: scaleY(0);}
.sec5-line-1:before { top: 10%; }
.sec5-line-1:after { left: 22.5%; }
.sec5-line-2:before { top: 32.5%; }
.sec5-line-2:after { left: 50%; }
.sec5-line-3:before { top: 55%; }
.sec5-line-3:after { right: 22.5%; }

.sec5-card:hover {
  box-shadow: 0px 3px 6px var(--card-hover-box-shadow-1), 0px var(--card-hover-box-shadow-2-y) var(--card-hover-box-shadow-2-blur) var(--card-hover-box-shadow-2), 0 0 0 1px var(--card-hover-border-color);
}
.sec5-card:hover .sec5-icon::after {
  background-color: var(--card-hover-icon-background-color);
  border-color: var(--card-hover-icon-border-color);
}
.sec5-card:hover .sec5-icon svg {
  color: var(--card-hover-icon-color);
}
.sec5-card:hover .sec5-shine {
  opacity: 1;
  transition-duration: .5s;
  transition-delay: 0s;
}
.sec5-card:hover .sec5-tiles {
  opacity: 1;
  transition-delay: .25s;
}
.sec5-card:hover .sec5-line {
  opacity: 1;
  transition-duration: .15s;
}
.sec5-card:hover .sec5-line:before {
  transform: scaleX(1);
}
.sec5-card:hover .sec5-line:after {
  transform: scaleY(1);
}

.sec5-day-night {
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 20px;
  opacity: .3;
}
.sec5-day-night input {
  display: none;
}
.sec5-day-night input + div {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: relative;
  box-shadow: inset 8px -8px 0 0 var(--text-color);
  transform: scale(1) rotate(-2deg);
  transition: box-shadow .5s ease 0s, transform .4s ease .1s;
}
.sec5-day-night input:checked + div {
  box-shadow: inset 20px -20px 0 0 var(--text-color);
  transform: scale(.5) rotate(0deg);
  transition: transform .3s ease .1s, box-shadow .2s ease 0s;
}
.sec5-twitter {
  position: fixed;
  display: block;
  right: 12px;
  bottom: 12px;
  z-index: 10;
}
.sec5-twitter svg {
  width: 32px;
  height: 32px;
  fill: #fff;
}

@media (max-width: 900px) {
  .sec5-grid {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, minmax(200px, 1fr));
    width: 95vw;
    height: auto;
    grid-gap: 24px;
  }
  .sec5-card {
    max-width: 95vw;
    max-height: 250px;
  }
}
