@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap');

.lux-header {
  width: 100vw;
  background: rgba(22, 22, 24); /* 얇고, 반투명 */
  box-shadow: 0 2px 16px rgba(0,0,0,0.07);
  backdrop-filter: blur(8px); /* 고급스러운 유리 효과 */
  position: fixed;
  top: 0; left: 0;
  z-index: 1000;
  transition: background 0.3s;
  font-family: 'Montserrat', Arial, sans-serif;
}

.lux-header-inner {
  max-width: 1280px;
  margin: 0 auto;
  height: 46px; /* 얇게 */
  min-height: 46px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 36px;
}

.lux-logo span {
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: 0.07em;
  color: #fff;
  background: linear-gradient(90deg, #A0D3F2 0%, #D56964 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.lux-nav ul {
  display: flex;
  gap: 22px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.lux-nav a {
  color: #e0e0e0;
  font-size: 0.98rem;
  font-weight: 500;
  text-decoration: none;
  letter-spacing: 0.01em;
  position: relative;
  transition: color 0.22s;
  padding: 2px 0;
}

.lux-nav a::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #A0D3F2 0%, #D56964 100%);
  transition: width 0.32s cubic-bezier(.4,0,.2,1);
  position: absolute;
  left: 0; bottom: -4px;
  border-radius: 2px;
}

.lux-nav a:hover {
  color: #b3e5fc;
}
.lux-nav a:hover::after {
  width: 100%;
}

.lux-header-actions {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.lux-btn {
  border: none;
  padding: 7px 18px;
  border-radius: 18px;
  font-size: 0.93rem;
  font-family: 'Montserrat', Arial, sans-serif;
  font-weight: 600;
  cursor: pointer;
  background: linear-gradient(90deg, #b3e5fc 0%, #e7e7e7 100%);
  color: #181818;
  box-shadow: 0 2px 12px rgba(179,229,252,0.10);
  letter-spacing: 0.03em;
  transition: background 0.2s, color 0.2s;
}
.lux-btn-primary:hover {
  background: linear-gradient(90deg, #fff 0%, #b3e5fc 100%);
  color: #222;
}

@media (max-width: 900px) {
  .lux-header-inner { padding: 0 10px; }
  .lux-logo span { font-size: 1rem; }
  .lux-nav ul { gap: 10px; }
  .lux-header-actions .lux-btn { padding: 6px 14px; font-size: 0.91rem; }
}
@media (max-width: 600px) {
  .lux-header-inner { flex-direction: column; height: auto; padding: 6px 2vw; gap: 10px; }
  .lux-nav ul { flex-wrap: wrap; gap: 8px; }
  .lux-header-actions { gap: 0.2rem; }
}
