/* service.css */
.serv-hero-bg {
  min-height: 100vh;
  background: linear-gradient(120deg, #fff 0%, #f5fbff 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  font-family: 'Poppins', 'Pretendard', sans-serif;
}

.serv-title {
  font-size: 2.2rem;
  font-weight: 900;
  color: #000000;
  letter-spacing: 0.09em;
  margin-bottom: 2.5vw;
  margin-top: 2vw;
  text-align: center;
  text-shadow: 0 2px 12px rgba(160, 211, 242, 0.09);
  z-index: 10;
}

.serv-hero-main {
  position: relative;
  z-index: 2;
  width: 70vw;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.serv-hero-image-wrap {
  position: relative;
  width: 100%;
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 8px 32px 0 rgba(160, 211, 242, 0.18);
  background: #fff;
  aspect-ratio: 16 / 9;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: box-shadow 0.18s, transform 0.18s;
}
.serv-hero-image-clickable:hover,
.serv-hero-image-clickable:focus {
  box-shadow: 0 12px 40px 0 #f2a0a0;
  transform: scale(1.025);
}

.serv-hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 18px;
  filter: brightness(0.97) contrast(1.04);
}

.serv-hero-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -60%);
  font-size: 4vw;
  font-weight: 900;
  color: #000000;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-shadow: 0 4px 24px rgba(160, 211, 242, 0.18);
  z-index: 3;
  pointer-events: none;
  white-space: nowrap;
}

.serv-hero-nav {
  margin-top: 3vw;
  display: flex;
  gap: 28px;
  z-index: 5;
  justify-content: center;
  align-items: center;
}

.serv-hero-nav-btn {
  background: transparent;
  border: none;
  font-size: 1.35rem;
  font-weight: 700;
  color: #A0D3F2;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  cursor: pointer;
  padding: 8px 20px;
  border-radius: 18px;
  transition: color 0.2s, background 0.2s;
  outline: none;
  opacity: 0.7;
}

.serv-hero-nav-btn.active,
.serv-hero-nav-btn:hover,
.serv-hero-nav-btn:focus {
  color: #fff;
  background: #A0D3F2;
  opacity: 1;
}

@media (max-width: 900px) {
  .serv-title { font-size: 1.5rem; }
  .serv-hero-title { font-size: 7vw; }
  .serv-hero-main { width: 96vw; }
  .serv-hero-image-wrap { aspect-ratio: 16/10; }
}

@media (max-width: 600px) {
  .serv-title { font-size: 1.18rem; margin-bottom: 12px; }
  .serv-hero-title { font-size: 9vw; }
  .serv-hero-main { width: 100vw; }
  .serv-hero-nav-btn { font-size: 1rem; padding: 6px 10px; }
}
