@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Roboto:wght@400;700&display=swap');

.contact-section.contact-light {
  background: #fff;
  min-height: 100vh;
  padding: 0;
  font-family: 'Montserrat', 'Roboto', 'Pretendard', sans-serif;
  color: #222;
}

.contact-flex-row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 90px;
  max-width: 1300px;
  margin: 0 auto;
  padding: 120px 7vw 80px 7vw;
}

.contact-info-area {
  flex: 1 1 0;
  min-width: 320px;
  color: #222;
  background: none;
  border: none;
  box-shadow: none;
  padding-right: 2vw;
}

.contact-info-area h1 {
  font-size: 3.2rem;
  font-weight: 400;
  letter-spacing: 0.02em;
  margin-bottom: 1.2em;
  line-height: 1.16;
  font-family: 'Montserrat', 'Roboto', sans-serif;
}

.contact-desc-en {
  font-size: 1.22rem;
  font-weight: 400;
  color: #222;
  margin-bottom: 2.2em;
  opacity: 0.85;
  line-height: 1.6;
  font-family: 'Montserrat', 'Roboto', sans-serif;
}

.contact-desc-ko {
  font-size: 1.08rem;
  color: #666;
  font-weight: 400;
  line-height: 1.7;
  opacity: 0.8;
  margin-bottom: 2.8em;
  font-family: 'Pretendard', 'Montserrat', sans-serif;
}

.contact-info-area ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.contact-info-area li {
  margin-bottom: 15px;
  font-size: 1.05rem;
  color: #444;
  display: flex;
  gap: 12px;
  align-items: flex-start;
  line-height: 1.6;
}

.contact-label {
  min-width: 70px;
  color: #2196f3;
  font-weight: 600;
  font-size: 1.06rem;
}

.contact-info-area a {
  color: #2196f3;
  text-decoration: underline;
  transition: color 0.2s;
}
.contact-info-area a:hover {
  color: #1565c0;
}

.contact-form-area {
  flex: 1 1 0;
  min-width: 340px;
  color: #222;
  background: none;
  border: none;
  box-shadow: none;
}

.contact-form-area h2 {
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 28px;
  color: #222;
  letter-spacing: 0.04em;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.contact-form label {
  font-size: 1.07rem;
  font-weight: 500;
  color: #555;
  margin-bottom: 5px;
  display: flex;
  flex-direction: column;
  gap: 7px;
}

.contact-form input,
.contact-form textarea {
  border: none;
  border-bottom: 1.5px solid #2196f3;
  border-radius: 0;
  padding: 12px 2px 10px 2px;
  font-size: 1.09rem;
  font-family: inherit;
  background: transparent;
  color: #222;
  transition: border-color 0.2s, background 0.2s;
  resize: none;
}

.contact-form input:focus,
.contact-form textarea:focus {
  border-color: #1565c0;
  outline: none;
  background: #f3f7fa;
}

.contact-btn {
  margin-top: 10px;
  padding: 13px 0;
  background: none;
  color: #2196f3;
  border: 1.5px solid #2196f3;
  border-radius: 0;
  font-size: 1.13rem;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, border-color 0.2s;
  box-shadow: none;
}
.contact-btn:hover {
  background: #2196f3;
  color: #fff;
  border-color: #1565c0;
}

.contact-success {
  background: none;
  color: #2196f3;
  font-weight: 600;
  font-size: 1.13rem;
  border-radius: 0;
  padding: 32px 0 0 0;
  text-align: left;
  margin-top: 30px;
  box-shadow: none;
}

.contact-error {
  color: #ff5050;
  margin-top: 14px;
  font-size: 1.01rem;
  font-weight: 500;
}

@media (max-width: 900px) {
  .contact-flex-row {
    flex-direction: column;
    gap: 40px;
    padding: 60px 4vw 40px 4vw;
  }
  .contact-info-area, .contact-form-area {
    min-width: 0;
    width: 100%;
  }
  .contact-info-area h1 {
    font-size: 2.1rem;
  }
}

@media (max-width: 600px) {
  .contact-flex-row {
    gap: 28px;
    padding: 36px 2vw 24px 2vw;
  }
  .contact-info-area h1 {
    font-size: 1.2rem;
  }
  .contact-desc-en, .contact-desc-ko {
    font-size: 0.97rem;
  }
  .contact-info-area li {
    font-size: 0.97rem;
  }
  .contact-form label {
    font-size: 0.97rem;
  }
  .contact-form input, .contact-form textarea {
    font-size: 0.97rem;
  }
  .contact-btn {
    font-size: 1rem;
    padding: 11px 0;
  }
  .contact-success {
    font-size: 1rem;
    padding: 20px 0 0 0;
  }
}
