/* log.css */
.log-bg {
  min-height: 100vh;
  /* background: linear-gradient(120deg, #e3f0ff 0%, #b8e0ff 100%);  // 삭제 또는 주석처리 */
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.log-glass {
  position: absolute;
  inset: 0;
  z-index: 0;
  background: rgba(255,255,255,0.5);
  backdrop-filter: blur(8px);
  pointer-events: none;
}

.log-content {
  display: flex;
  max-width: 1500px;
  margin: 0 auto;
  padding: 20px 0 0 0;
  flex: 1;
  z-index: 1;
  position: relative;
}

.log-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 340px;
  padding-right: 150px;
  padding-left: 44px;
  animation: fadeInLeft 1s;
}

@keyframes fadeInLeft {
  from { opacity: 0; transform: translateX(-60px);}
  to { opacity: 1; transform: translateX(0);}
}

.log-contact-badge {
  background: #85f28b;
  color: #222;
  font-weight: 700;
  font-size: 1rem;
  border-radius: 22px;
  padding: 7px 20px;
  margin-bottom: 22px;
  display: inline-block;
  letter-spacing: 1px;
  box-shadow: 0 2px 12px rgba(56,182,255,0.06);
}

.log-left h1 {
  font-size: 3.1rem;
  font-weight: 900;
  margin-bottom: 18px;
  color: #222;
  letter-spacing: -1.5px;
  line-height: 1.1;
}

.log-left .highlight {
  color: #38b6ff;
  background: linear-gradient(90deg, #38b6ff 70%, #85f28b 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.log-desc {
  font-size: 1.23rem;
  color: #222;
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.7;
  opacity: 0.88;
}

.desc-strong {
  color: #38b6ff;
  font-weight: 700;
}

.log-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 900px;
  margin-left: auto;
  margin-right: 40px;
  background: rgba(255,255,255,0.92);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.13);
  padding: 44px 36px 36px 36px;
  animation: fadeInUp 1.1s;
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(40px);}
  to { opacity: 1; transform: translateY(0);}
}

.log-form {
  width: 320px;
}

.log-step {
  margin-bottom: 40px;
}

.log-step-label {
  font-size: 1.08rem;
  font-weight: 700;
  margin-bottom: 14px;
  padding: 10px 26px;
  border-radius: 22px;
  display: inline-block;
  color: #fff;
  background: #38b6ff;
  box-shadow: 0 2px 10px rgba(56,182,255,0.07);
  opacity: 0.93;
  transition: background 0.18s, box-shadow 0.18s;
}
.log-step-label.active {
  background: #236ddf;
  box-shadow: 0 4px 18px rgba(35,109,223,0.13);
}

.log-input-row {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-top: 7px;
}

.log-input-row input {
  flex: 1;
  padding: 15px 20px;
  border: 1.5px solid #222;
  border-radius: 9px;
  font-size: 1.15rem;
  background: #f9fbff;
  color: #222;
  outline: none;
  transition: border 0.17s, box-shadow 0.17s;
  box-shadow: 0 2px 10px rgba(56,182,255,0.03);
}

.log-input-row input:focus {
  border: 1.5px solid #38b6ff;
  background: #fff;
  box-shadow: 0 4px 18px rgba(56,182,255,0.11);
}

.log-btn {
  padding: 15px 0;
  border: none;
  border-radius: 9px;
  font-size: 1.13rem;
  font-weight: 700;
  cursor: pointer;
  background: #111;
  color: #fff;
  transition: background 0.15s, color 0.15s, box-shadow 0.15s;
  box-shadow: 0 2px 10px rgba(56,182,255,0.07);
  margin-top: 8px;
}

.log-btn:disabled {
  background: #ddd;
  color: #aaa;
  cursor: not-allowed;
  box-shadow: none;
}

.log-message {
  margin-top: 24px;
  color: #0072ff;
  font-size: 1.14rem;
  font-weight: 600;
  min-height: 22px;
  text-align: center;
  letter-spacing: 0.01em;
  transition: color 0.18s;
}
.log-message.success {
  color: #38b6ff;
}

@media (max-width: 900px) {
  .log-content {
    flex-direction: column;
    align-items: center;
    padding: 40px 0 0 0;
  }
  .log-left {
    align-items: center;
    padding-left: 0;
    margin-bottom: 38px;
  }
  .log-right {
    margin: 0 auto 0 auto;
    min-width: 0;
    width: 96vw;
    max-width: 99vw;
  }
}

@media (max-width: 600px) {
  .log-content {
    padding: 18px 0 0 0;
  }
  .log-left h1 {
    font-size: 2.0rem;
  }
  .log-right {
    padding: 16px 4px 12px 4px;
  }
}
