@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap');

.sec8-footer {
  width: 100vw;
  background: #393939;
  color: #ededed;
  font-family: 'Inter', Arial, sans-serif;
  padding: 60px 0 40px 0;
}

.sec8-footer-inner {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: 60px;
  padding: 0 40px;
}

.sec8-logo-col {
  flex: 1 0 180px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.sec8-logo {
  font-size: 2.6rem;
  font-weight: 700;
  letter-spacing: -2px;
  color: #fff;
  margin-bottom: 10px;
  font-family: 'Inter', Arial, sans-serif;
}

.sec8-links-col {
  flex: 4 1 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  gap: 64px;
  width: 100%;
}

.sec8-link-group {
  min-width: 180px;
  margin-right: 12px;
}

.sec8-link-heading {
  font-size: 0.98rem;
  font-weight: 500;
  color: #bdbdbd;
  text-transform: uppercase;
  margin-bottom: 15px;
  letter-spacing: 0.08em;
  border-bottom: 2px solid transparent;
  border-bottom: 1.5px solid #bdbdbd;
  width: max-content;
  padding-bottom: 2px;
}

.sec8-link-group ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sec8-link-group li {
  font-size: 1.05rem;
  font-weight: 400;
  color: #ededed;
  margin-bottom: 11px;
  transition: color 0.2s;
  cursor: pointer;
}
.sec8-link-group li:last-child {
  margin-bottom: 0;
}
.sec8-link-group li:hover {
  color: #fff;
  text-decoration: underline;
}

@media (max-width: 900px) {
  .sec8-footer-inner {
    flex-direction: column;
    align-items: flex-start;
    gap: 36px;
    padding: 0 18px;
  }
  .sec8-links-col {
    gap: 28px;
    flex-wrap: wrap;
  }
  .sec8-link-group {
    min-width: 140px;
    margin-right: 0;
  }
}

@media (max-width: 600px) {
  .sec8-footer-inner {
    flex-direction: column;
    gap: 25px;
    padding: 0 8px;
  }
  .sec8-links-col {
    flex-direction: column;
    gap: 18px;
  }
  .sec8-link-group {
    min-width: 0;
  }
  .sec8-logo {
    font-size: 2rem;
  }
}
