/* list.css (페이지네이션 포함 전체) */
.list-wrap {
  width: 100%;
  background: #fff;
  min-height: 100vh;
  font-family: 'SUIT', 'Pretendard', 'sans-serif';
  padding-top: 20px;
}

.list-title-top {
  width: 100%;
  padding: 38px 0 12px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 18px;
}

.list-title-top h1 {
  font-size: 2.2rem;
  font-weight: 800;
  letter-spacing: -1.2px;
  color: #232323;
  margin: 0;
}

.list-search-box {
  width: 380px;
  max-width: 90vw;
  display: flex;
  justify-content: center;
}

.list-search-input {
  width: 100%;
  padding: 11px 18px;
  border: 1.5px solid #e0e0e0;
  border-radius: 22px;
  font-size: 1.08rem;
  background: #fafafa;
  color: #222;
  outline: none;
  transition: border 0.19s;
}

.list-search-input:focus {
  border: 1.5px solid #A0D3F2;
  background: #fff;
}

.filter-bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-bottom: 8px;
}

.category-bar {
  display: flex;
  gap: 12px;
  padding: 18px 0 0 0;
  width: 100%;
  justify-content: center;
  flex-wrap: wrap;
}

.category-btn {
  border: none;
  background: #f5f5f5;
  color: #222;
  font-size: 1.07rem;
  padding: 9px 24px;
  border-radius: 22px;
  margin-bottom: 10px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
}

.category-btn.active {
  background: #222;
  color: #fff;
}

.subcategory-bar {
  display: flex;
  gap: 24px;
  justify-content: center;
  padding: 0 0 0 0;
  border-bottom: 1.5px solid #eee;
  margin-bottom: 8px;
  margin-top: 8px;
  flex-wrap: wrap;
}

.subcategory-btn {
  border: none;
  background: none;
  color: #888;
  font-size: 1.11rem;
  font-weight: 500;
  padding: 0 0 16px 0;
  cursor: pointer;
  border-bottom: 2.5px solid transparent;
  transition: color 0.18s, border-bottom 0.18s;
}

.subcategory-btn.active {
  color: #111;
  border-bottom: 2.5px solid #111;
}

.reset-btn {
  border: none;
  background: #f8f8f8;
  color: #555;
  font-size: 1.03rem;
  font-weight: 600;
  padding: 7px 28px;
  border-radius: 18px;
  margin: 10px 0 0 0;
  cursor: pointer;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
  transition: background 0.15s, color 0.15s;
}
.reset-btn:hover {
  background: #A0D3F2;
  color: #fff;
}

.list-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 36px 20px;
  padding: 36px 48px 0 48px;
}

.list-card {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.04);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: box-shadow 0.2s;
  min-height: 280px;
  cursor: pointer;
}

.list-card:focus {
  outline: 2px solid #A0D3F2;
}

.list-card:hover {
  box-shadow: 0 8px 28px rgba(0,0,0,0.10);
}

.list-thumb {
  width: 100%;
  aspect-ratio: 1.3/1;
  background: #fafafa;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.list-thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.list-info {
  width: 100%;
  padding: 18px 18px 10px 18px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.list-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list-name {
  font-size: 1.13rem;
  font-weight: 700;
  color: #222;
}

.list-category {
  font-size: 1rem;
  color: #A0D3F2;
  font-weight: 600;
  padding: 2px 10px;
  background: #eafaf6;
  border-radius: 10px;
}

.list-company {
  font-size: 1rem;
  color: #666;
  font-weight: 500;
}

.list-date {
  font-size: 0.97rem;
  color: #aaa;
  font-weight: 400;
}

.list-category2 {
  font-size: 0.97rem;
  color: #3b82f6;
  font-weight: 500;
  margin-top: 2px;
}

.list-empty {
  grid-column: 1 / -1;
  text-align: center;
  color: #aaa;
  font-size: 1.1rem;
  padding: 40px 0;
}

/* 페이지네이션 */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 36px 0 48px 0;
  gap: 8px;
}

.page-btn {
  border: none;
  background: #f5f5f5;
  color: #333;
  font-size: 1.08rem;
  padding: 7px 16px;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.14s, color 0.14s;
}

.page-btn.active,
.page-btn:hover {
  background: #A0D3F2;
  color: #fff;
}

.page-btn:disabled {
  background: #eee;
  color: #bbb;
  cursor: not-allowed;
}

/* 모달 스타일 */
.list-modal-backdrop {
  position: fixed;
  z-index: 1000;
  inset: 0;
  background: rgba(0,0,0,0.36);
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.25s;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.list-modal {
  background: #fff;
  border-radius: 18px;
  max-width: 600px;
  width: 90vw;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  padding: 32px 32px 24px 32px;
  position: relative;
  animation: modalPop 0.22s;
}

@keyframes modalPop {
  from { transform: translateY(40px) scale(0.97); opacity: 0; }
  to   { transform: translateY(0) scale(1); opacity: 1; }
}

.list-modal-close {
  position: absolute;
  top: 18px;
  right: 22px;
  background: none;
  border: none;
  font-size: 2.1rem;
  color: #888;
  cursor: pointer;
  z-index: 1;
}

.list-modal-img {
  width: 100%;
  aspect-ratio: 1.6/1;
  background: #fafafa;
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 22px;
}
.list-modal-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.list-modal-content h2 {
  font-size: 1.45rem;
  font-weight: 800;
  color: #222;
  margin-bottom: 8px;
}

.list-modal-row {
  display: flex;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 7px;
}

.list-modal-category,
.list-modal-category2 {
  font-size: 1rem;
  font-weight: 600;
  color: #0037ff;
  background: #eafaf6;
  padding: 2px 10px;
  border-radius: 10px;
}

.list-modal-category2 {
  color: #3b82f6;
  background: #eaf2fb;
}

.list-modal-company {
  font-size: 1rem;
  color: #666;
  font-weight: 500;
}

.list-modal-date {
  font-size: 0.97rem;
  color: #aaa;
  font-weight: 400;
}

.list-modal-desc {
  margin: 18px 0 10px 0;
  font-size: 1.08rem;
  color: #232323;
}

.list-modal-desc strong {
  display: block;
  font-size: 1.01rem;
  color: #A0D3F2;
  margin-bottom: 4px;
}

.list-modal-detail {
  font-size: 0.98rem;
  color: #444;
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-top: 10px;
}

.list-modal-detail strong {
  color: #3b82f6;
  margin-right: 4px;
}

.apply-btn {
  margin-top: 22px;
  width: 100%;
  padding: 13px 0;
  background: #A0D3F2;
  color: #fff;
  font-size: 1.13rem;
  font-weight: 700;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.18s;
}
.apply-btn:hover {
  background: #48a9e5;
}




/* list.css - 카테고리2 슬라이드 및 버튼 크기 고정, 복수 선택 지원 */
.subcategory-bar {
  display: flex;
  gap: 24px;
  justify-content: center;
  padding: 0 0 0 0;
  border-bottom: 1.5px solid #eee;
  margin-bottom: 8px;
  margin-top: 8px;
  flex-wrap: nowrap;
  align-items: center;
  cursor: grab;
  user-select: none;
  min-width: 520px;
  max-width: 100vw;
  min-height: 48px;
  height: 48px;
}

.subcategory-bar:active {
  cursor: grabbing;
}

.cat2-slide-btn {
  background: #f5f5f5;
  color: #A0D3F2;
  border: none;
  font-size: 1.5rem;
  font-weight: 700;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 0 4px;
  transition: background 0.18s, color 0.18s;
  box-shadow: 0 1px 4px rgba(160,211,242,0.07);
}
.cat2-slide-btn:disabled {
  color: #c9e4f6;
  background: #f2f8fc;
  cursor: not-allowed;
}
.cat2-slide-btn:not(:disabled):hover {
  background: #A0D3F2;
  color: #fff;
}

.subcategory-btn {
  border: none;
  background: none;
  color: #888;
  font-size: 1.11rem;
  font-weight: 500;
  padding: 0 0 16px 0;
  cursor: pointer;
  border-bottom: 2.5px solid transparent;
  transition: color 0.18s, border-bottom 0.18s;
  min-width: 78px;
  max-width: 120px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  height: 32px;
  box-sizing: border-box;
}
.subcategory-btn.active {
  color: #111;
  border-bottom: 2.5px solid #111;
}

.all-cat-btn {
  background: #A0D3F2;
  color: #fff;
  border: none;
  font-size: 1.01rem;
  font-weight: 600;
  border-radius: 12px;
  padding: 7px 18px;
  margin-left: 18px;
  cursor: pointer;
  transition: background 0.16s, color 0.16s;
  height: 32px;
}
.all-cat-btn:hover {
  background: #48a9e5;
}

/* 모두 보기 모달 */
.all-cat-modal-backdrop {
  position: fixed;
  z-index: 1002;
  inset: 0;
  background: rgba(0,0,0,0.38);
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.22s;
}
.all-cat-modal {
  background: #fff;
  border-radius: 18px;
  max-width: 700px;
  width: 96vw;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  padding: 34px 28px 28px 28px;
  position: relative;
  animation: modalPop 0.18s;
}
.all-cat-modal-close {
  position: absolute;
  top: 16px;
  right: 18px;
  background: none;
  border: none;
  font-size: 2.1rem;
  color: #888;
  cursor: pointer;
  z-index: 1;
}
.all-cat-modal h2 {
  font-size: 1.4rem;
  font-weight: 800;
  color: #222;
  margin-bottom: 16px;
  text-align: center;
}
.all-cat-section {
  margin-bottom: 18px;
}
.all-cat-section h3 {
  font-size: 1.07rem;
  color: #48a9e5;
  margin-bottom: 8px;
  font-weight: 700;
}
.all-cat-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 12px;
  margin-bottom: 8px;
}
.all-cat-btn {
  background: #f5f5f5;
  color: #222;
  border: none;
  font-size: 1.01rem;
  font-weight: 600;
  border-radius: 12px;
  padding: 7px 16px;
  cursor: pointer;
  transition: background 0.16s, color 0.16s;
}
.all-cat-btn.active,
.all-cat-btn:hover {
  background: #A0D3F2;
  color: #fff;
}
.all-cat-modal-done {
  margin-top: 12px;
  width: 100%;
  padding: 13px 0;
  background: #A0D3F2;
  color: #fff;
  font-size: 1.13rem;
  font-weight: 700;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.18s;
}
.all-cat-modal-done:hover {
  background: #48a9e5;
}

/* 이하 기존 list.css 동일 */
