@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard-dynamic-subset.css');

#sec2-inc01 {
  padding-top: 150px;
  min-height: 900px;
  background: #fff;

  font-family: 'Pretendard';
  display: flex;
  align-items: center;
  justify-content: center;
}

.sec2-inner {
  display: flex;
  width: 100%;
  max-width: 1500px;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
  min-height: 600px;
  gap: 5vw;
}

.sec2-big_txt {
  flex: 1 1 0;
  position: relative;
  padding-left: 70px;
  font-size: 40px;           /* 기존 30px → 48px */
  font-weight: 800;
  line-height: 1.18;
  color: rgba(0,0,0,.85);
  min-width: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.sec2-b_txt {
  position: relative;
  width: max-content;
  background: linear-gradient(to right, #000000, #a0d3f2, #fc0000) no-repeat;
  -webkit-background-clip: text;
  background-clip: text;
  background-size: 0%;
  transition: background-size cubic-bezier(.1,.5,.5,1) 0.5s;
  color: transparent;
  -webkit-text-fill-color: transparent;
  margin: 0 0 22px 0;      /* 더 넓은 간격 */
  font-size: 1em;          /* 상위에서 컨트롤 */
}

.sec2-img_wrap {
  flex: 1 1 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  min-width: 0;
}

.sec2-img {
  width: 650px;
  max-width: 650px; 
  margin-right: 70px;       /* 기존 400px → 600px */
  height: auto;
  border-radius: 24px;     /* 더 둥글게 */
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  opacity: 0;
  transform: translateX(100px);
  will-change: opacity, transform;
}

@media (max-width: 1200px) {
  .sec2-inner { max-width: 98vw; gap: 3vw; }
  .sec2-big_txt { padding-left: 3vw; font-size: 34px; }
  .sec2-img { max-width: 380px; }
}
@media (max-width: 900px) {
  .sec2-inner {
    flex-direction: column;
    align-items: flex-start;
    gap: 40px;
  }
  .sec2-img_wrap {
    justify-content: center;
    width: 100%;
  }
  .sec2-img {
    max-width: 80vw;
  }
  .sec2-big_txt {
    padding-left: 6vw;
    font-size: 26px;
  }
}
@media (max-width: 600px) {
  .sec2-big_txt { font-size: 18px; padding-left: 2vw; }
  .sec2-img { max-width: 98vw; }
}
