/* log.css (하단 정보 제외, 위와 동일) */
.log-bg {
  min-height: 100vh;
  background: linear-gradient(90deg, #f6fbff 0%, #b8e0ff 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.log-content {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  padding: 80px 0 0 0;
  flex: 1;
}

.log-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-width: 340px;
  padding-left: 32px;
}

.log-contact-badge {
  background: #85f28b;
  color: #222;
  font-weight: 700;
  font-size: 1rem;
  border-radius: 22px;
  padding: 6px 18px;
  margin-bottom: 18px;
  display: inline-block;
}

.log-left h1 {
  font-size: 3.2rem;
  font-weight: 800;
  margin-bottom: 18px;
  color: #222;
  letter-spacing: -1.5px;
}

.log-desc {
  font-size: 1.18rem;
  color: #222;
  margin-top: 0;
  margin-bottom: 0;
  line-height: 1.7;
  opacity: 0.85;
}

.log-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 380px;
  max-width: 480px;
  margin-left: auto;
  margin-right: 32px;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.10);
  padding: 38px 32px 32px 32px;
}

.log-step {
  margin-bottom: 32px;
}

.log-step-label {
  font-size: 1.08rem;
  font-weight: 700;
  margin-bottom: 10px;
  padding: 8px 22px;
  border-radius: 22px;
  display: inline-block;
  color: #fff;
}

.log-step-label.step1 { background: #38b6ff; }
.log-step-label.step2 { background: #2698e6; }
.log-step-label.step3 { background: #236ddf; }

.log-input-row {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 5px;
}

.log-input-row input {
  flex: 1;
  padding: 14px 18px;
  border: 1.5px solid #222;
  border-radius: 8px;
  font-size: 1.13rem;
  background: #fafcff;
  color: #222;
  outline: none;
  transition: border 0.17s;
}

.log-input-row input:focus {
  border: 1.5px solid #38b6ff;
  background: #fff;
}

.log-btn {
  padding: 13px 22px;
  border: none;
  border-radius: 8px;
  font-size: 1.13rem;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.15s, color 0.15s;
}

.log-btn.black {
  background: #111;
  color: #fff;
}

.log-btn:disabled {
  background: #ddd;
  color: #aaa;
  cursor: not-allowed;
}

.log-message {
  margin-top: 20px;
  color: #0072ff;
  font-size: 1.12rem;
  font-weight: 500;
  min-height: 22px;
  text-align: center;
}

@media (max-width: 900px) {
  .log-content {
    flex-direction: column;
    align-items: center;
    padding: 40px 0 0 0;
  }
  .log-left {
    align-items: center;
    padding-left: 0;
    margin-bottom: 38px;
  }
  .log-right {
    margin: 0 auto 0 auto;
    min-width: 0;
    width: 94vw;
    max-width: 98vw;
  }
}

@media (max-width: 600px) {
  .log-content {
    padding: 24px 0 0 0;
  }
  .log-left h1 {
    font-size: 2.0rem;
  }
  .log-right {
    padding: 22px 8px 18px 8px;
  }
}
