/* dashboard.css - 날짜별 일정표 및 대시보드 전체 스타일 */

/* 전체 레이아웃 */
.dash-root {
  display: flex;
  padding-top: 30px;
  min-height: 100vh;
  background: linear-gradient(120deg, #f5fbff 0%, #eafaf6 100%);
  font-family: 'SUIT', 'Pretendard', 'Poppins', sans-serif;
}

/* 메인 대시보드 영역 */
.dash-main {
  flex: 1 1 0%;
  padding: 38px 32px 0 32px;
  min-width: 0;
  background: #f7fafd;
  display: flex;
  flex-direction: column;
}
.dash-main-noside {
  max-width: 1200px;
  margin: 0 auto;
}

/* 대시보드 헤더 및 탭 */
.dash-main-header {
  display: flex;
  flex-direction: column;
  gap: 18px;
  margin-bottom: 28px;
}
.dash-main-header h2 {
  font-size: 2.2rem;
  font-weight: 900;
  color: #232323;
  margin-bottom: 0;
  letter-spacing: -1.2px;
}
.dash-tabs {
  display: flex;
  gap: 18px;
  margin-top: 4px;
}
.dash-tabs button {
  background: #fff;
  color: #38b6ff;
  border: 2px solid #A0D3F2;
  border-radius: 22px;
  padding: 12px 38px;
  font-size: 1.13rem;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.18s, color 0.18s, border 0.18s;
}
.dash-tabs button.active,
.dash-tabs button:hover {
  background: #A0D3F2;
  color: #fff;
  border-color: #38b6ff;
}

/* 섹션 및 테이블 */
.dash-section {
  margin-top: 0;
}
.dash-section-title {
  font-size: 1.18rem;
  font-weight: 800;
  color: #38b6ff;
  margin-bottom: 18px;
  margin-top: 0;
}
.dash-task-table {
  width: 100%;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 2px 12px #A0D3F224;
  padding: 0 0 10px 0;
  display: flex;
  flex-direction: column;
  gap: 0;
}
.dash-task-table-header {
  display: grid;
  grid-template-columns: 2.5fr 1.1fr 1.2fr 1fr 1.3fr;
  font-size: 1.01rem;
  color: #b0b8c1;
  font-weight: 700;
  padding: 22px 38px 10px 38px;
  background: #f7fafd;
  border-radius: 18px 18px 0 0;
}
.dash-task-row {
  display: grid;
  grid-template-columns: 2.5fr 1.1fr 1.2fr 1fr 1.3fr;
  align-items: center;
  padding: 15px 38px;
  border-bottom: 1px solid #f0f0f0;
  background: #fff;
  transition: background 0.14s;
  cursor: pointer;
}
.dash-task-row:last-child {
  border-bottom: none;
}
.dash-task-title {
  display: flex;
  align-items: center;
  gap: 14px;
  font-size: 1.07rem;
  font-weight: 600;
}
.dash-task-title img {
  width: 48px;
  height: 38px;
  object-fit: cover;
  border-radius: 10px;
  background: #eafaf6;
  box-shadow: 0 2px 10px #A0D3F210;
}
.dash-task-category {
  color: #A0D3F2;
  font-weight: 700;
  font-size: 1.01rem;
}
.dash-task-company {
  color: #666;
  font-size: 1.01rem;
  font-weight: 500;
}
.dash-task-date {
  color: #A0D3F2;
  font-size: 0.97rem;
  font-weight: 600;
  margin-left: 8px;
}
.dash-badge-applied {
  display: inline-block;
  background: #eafaf6;
  color: #38b6ff;
  font-size: 0.99rem;
  font-weight: 700;
  border-radius: 12px;
  padding: 5px 17px;
}
.dash-badge-completed {
  display: inline-block;
  background: #d1fae5;
  color: #1ebd7b;
  font-size: 0.99rem;
  font-weight: 700;
  border-radius: 12px;
  padding: 5px 17px;
}
.dash-task-actions {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-left: 18px;
}
.dash-btn-complete {
  background: linear-gradient(90deg, #A0D3F2 60%, #38b6ff 100%);
  color: #fff;
  border: none;
  border-radius: 12px;
  padding: 9px 20px;
  font-size: 1.01rem;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.16s;
}
.dash-btn-complete:hover {
  background: linear-gradient(90deg, #38b6ff 50%, #A0D3F2 100%);
}
.dash-btn-delete {
  background: #fff;
  color: #38b6ff;
  border: 2px solid #A0D3F2;
  border-radius: 12px;
  padding: 9px 20px;
  font-size: 1.01rem;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.16s, color 0.16s, border 0.16s;
}
.dash-btn-delete:hover {
  background: #A0D3F2;
  color: #fff;
  border-color: #38b6ff;
}
.dash-task-empty {
  color: #aaa;
  font-size: 1.13rem;
  text-align: center;
  padding: 38px 0;
}

/* 우측 일정표: 날짜별 보기 */
.dash-schedule-calendar {
  width: 340px;
  min-width: 240px;
  background: #f7fafd;
  padding: 38px 18px 0 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.dash-schedule-title {
  font-size: 1.22rem;
  font-weight: 900;
  color: #232323;
  margin-bottom: 18px;
  letter-spacing: 1px;
}
.dash-day-list {
  width: 100%;
}
.dash-day-list-group {
  margin-bottom: 24px;
}
.dash-day-list-label {
  font-size: 1.07rem;
  font-weight: 700;
  color: #1976d2;
  margin-bottom: 7px;
}
.dash-day-list-ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.dash-day-list-li {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 1.01rem;
  padding: 4px 0;
}
.dash-day-list-dot {
  display: inline-block;
  width: 8px;
  height: 8px;
  background: #1976d2;
  border-radius: 50%;
}
.dash-day-list-title {
  font-weight: 600;
  color: #232323;
}
.dash-day-list-company {
  color: #43a047;
  font-size: 0.97rem;
  margin-left: 6px;
}

/* 모달 팝업 (list와 동일한 가로형 구조) */
.list-modal-backdrop {
  position: fixed;
  z-index: 1000;
  inset: 0;
  background: rgba(0,0,0,0.36);
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeIn 0.25s;
}
@keyframes fadeIn {
  from { opacity: 0; }
  to   { opacity: 1; }
}
.list-modal.list-modal-horizontal {
  background: #fff;
  border-radius: 22px;
  max-width: 820px;
  width: 90vw;
  min-width: 340px;
  min-height: 340px;
  box-shadow: 0 8px 32px rgba(160, 211, 242, 0.22);
  padding: 0;
  position: relative;
  animation: modalPop 0.22s;
  display: flex;
  flex-direction: column;
}
@keyframes modalPop {
  from { transform: translateY(40px) scale(0.97); opacity: 0; }
  to   { transform: translateY(0) scale(1); opacity: 1; }
}
.list-modal-horizontal-inner {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  min-height: 340px;
}
.list-modal-img-horizontal {
  flex: 1.1;
  min-width: 270px;
  max-width: 340px;
  background: linear-gradient(120deg, #eafaf6 0%, #A0D3F2 100%);
  border-radius: 22px 0 0 22px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list-modal-img-horizontal img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 22px 0 0 22px;
  box-shadow: 0 4px 24px #A0D3F222;
}
.list-modal-content-horizontal {
  flex: 1.6;
  padding: 38px 38px 32px 38px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background: #fff;
  border-radius: 0 22px 22px 0;
  min-width: 0;
}
.list-modal-content-horizontal h2 {
  font-size: 1.7rem;
  font-weight: 800;
  color: #222;
  margin-bottom: 12px;
  letter-spacing: -1px;
}
.list-modal-row-horizontal {
  display: flex;
  gap: 10px;
  margin-bottom: 8px;
  align-items: center;
}
.list-modal-category,
.list-modal-category2 {
  font-size: 1rem;
  font-weight: 600;
  color: #38b6ff;
  background: #eafaf6;
  padding: 3px 13px;
  border-radius: 12px;
  margin-right: 6px;
  letter-spacing: 0.01em;
}
.list-modal-category2 {
  color: #A0D3F2;
  background: #eaf2fb;
}
.list-modal-company {
  font-size: 1.08rem;
  color: #555;
  font-weight: 500;
}
.list-modal-date {
  font-size: 1.01rem;
  color: #A0D3F2;
  font-weight: 700;
}
.list-modal-desc-horizontal {
  margin: 18px 0 10px 0;
  font-size: 1.13rem;
  color: #232323;
  line-height: 1.6;
}
.list-modal-desc-horizontal strong {
  display: block;
  font-size: 1.01rem;
  color: #38b6ff;
  margin-bottom: 4px;
}
.list-modal-detail-horizontal {
  font-size: 1.01rem;
  color: #444;
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin-top: 10px;
  margin-bottom: 18px;
}
.list-modal-detail-horizontal strong {
  color: #A0D3F2;
  margin-right: 4px;
}
.list-modal-close {
  position: absolute;
  top: 18px;
  right: 22px;
  background: none;
  border: none;
  font-size: 2.1rem;
  color: #A0D3F2;
  cursor: pointer;
  z-index: 2;
  transition: color 0.18s;
}
.list-modal-close:hover {
  color: #38b6ff;
}

/* 반응형 */
@media (max-width: 1200px) {
  .dash-root { flex-direction: column; }
  .dash-main { padding: 24px 6vw 0 6vw; }
  .dash-schedule-calendar { width: 100vw; min-width: 0; padding: 18px 2vw 0 2vw; align-items: flex-start; }
}
@media (max-width: 900px) {
  .dash-main { padding: 18px 2vw 0 2vw; }
  .dash-task-table-header, .dash-task-row {
    grid-template-columns: 1.8fr 1fr 1fr 1fr 1.3fr;
    padding: 12px 6px;
  }
  .dash-schedule-calendar { padding: 12px 1vw 0 1vw; }
}
@media (max-width: 600px) {
  .dash-main-header h2 { font-size: 1.1rem; }
  .dash-section-title { font-size: 1.01rem; }
  .dash-task-table-header, .dash-task-row {
    font-size: 0.93rem;
    padding: 7px 2px;
    grid-template-columns: 1.5fr 1fr 1fr 0.9fr 1.1fr;
  }
  .dash-task-title img { width: 36px; height: 28px; }
  .dash-schedule-title { font-size: 1rem; }
  .dash-day-list-label { font-size: 0.97rem; }
  .list-modal-content-horizontal h2 { font-size: 1.15rem; }
  .list-modal-content-horizontal { padding: 16px 6px 10px 6px; }
}
