/* add.css */
.add-contact-bg {
  min-height: 100vh;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Pretendard', 'Poppins', sans-serif;
}

.add-contact-container {
  display: flex;
  max-width: 1200px;
  width: 100%;
  background: #fff;
  margin: 40px 0;
  box-sizing: border-box;
  padding: 40px 32px;
  box-shadow: 0 4px 32px rgba(160, 211, 242, 0.08);
  border-radius: 18px;
  gap: 56px;
}

.add-contact-left {
  flex: 1.2;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-width: 340px;
  padding-right: 16px;
}

.add-contact-title {
  font-size: 2.7rem;
  font-weight: 700;
  margin-bottom: 24px;
  color: #222;
  letter-spacing: 0.02em;
}

.add-contact-desc {
  font-size: 1.14rem;
  color: #222;
  margin-bottom: 32px;
  line-height: 1.7;
  opacity: 0.9;
}

.add-contact-desc-sub {
  color: #4c6378;
  font-size: 1rem;
  display: block;
  margin-top: 10px;
  margin-bottom: 6px;
  opacity: 0.8;
}

.add-contact-info {
  margin-top: 18px;
  font-size: 1.08rem;
  color: #222;
  line-height: 1.8;
}

.add-contact-info-label {
  font-weight: 700;
  color: #40a0e5;
  margin-right: 12px;
  min-width: 56px;
  display: inline-block;
}

.add-contact-link {
  color: #40a0e5;
  text-decoration: underline;
  font-weight: 600;
}

.add-contact-form {
  flex: 1.1;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  gap: 18px;
}

.add-form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 4px;
}

.add-form-group label {
  font-size: 1.08rem;
  font-weight: 600;
  color: #222;
  margin-bottom: 7px;
}

.add-form-group input,
.add-form-group select,
.add-form-group textarea {
  font-size: 1.08rem;
  padding: 13px 14px;
  border: none;
  border-bottom: 2px solid #A0D3F2;
  background: #f9fbff;
  border-radius: 6px 6px 0 0;
  color: #222;
  outline: none;
  transition: border-color 0.18s, background 0.18s;
  resize: none;
}

.add-form-group input:focus,
.add-form-group select:focus,
.add-form-group textarea:focus {
  border-bottom: 2px solid #40a0e5;
  background: #fff;
}

.add-contact-btn {
  margin-top: 18px;
  padding: 15px 0;
  width: 100%;
  font-size: 1.15rem;
  font-weight: 700;
  color: #40a0e5;
  background: #fff;
  border: 2px solid #A0D3F2;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
}

.add-contact-btn:hover,
.add-contact-btn:focus {
  background: #A0D3F2;
  color: #fff;
}

@media (max-width: 900px) {
  .add-contact-container {
    flex-direction: column;
    padding: 24px 8px;
    gap: 28px;
  }
  .add-contact-left {
    min-width: 0;
    padding-right: 0;
  }
  .add-contact-form {
    min-width: 0;
  }
}
