@import url('https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard-dynamic-subset.css');

#sec3-inc02 {
  display: flex;
  align-items: flex-end;
  padding: 20vh 5%;
  font-size: 16px;
  color: rgba(0, 0, 0, 0.4);
  line-height: 1.65;
  background: #ffffff;
  font-family: 'Pretendard';
  margin-bottom: 100px;
}
.sec3-tit_area {
  flex-shrink: 0;
  padding-top: 70px;
}
.sec3-tit_area p {
  margin-bottom: 25px;
  font-size: 65px;
  font-weight: 700;
  color: #000000;
}
.sec3-tit_area .sec3-pl {
  font-size: 17px;
  white-space: pre-line;
  line-height: 1.8;
}

#sec3-inc02 ul {
  display: flex;
  flex-grow: 1;
  margin-left: 250px;
}
#sec3-inc02 ul li {
  width: 33%;
  text-align: center;
}
#sec3-inc02 ul li span.sec3-counting {
  font-size: 65px;
  font-weight: 700;
  color: #000000;
  white-space: nowrap;
  font-family: 'Poppins', 'Pretendard', sans-serif;
}
#sec3-inc02 ul li p i {
  font-size: 45px;
  font-weight: 700;
  color: #000;
  font-style: normal;
  font-family: 'Poppins', 'Pretendard', sans-serif;
}
#sec3-inc02 ul li .sec3-tit {
  margin: 5px 0 30px;
  font-size: 30px;
  font-weight: 600;
  color: #000;
  line-height: 1.4;
  word-break: keep-all;
}
#sec3-inc02 ul li div {
  white-space: pre-line;
  word-break: keep-all;
}

@media (max-width: 1500px) {
  #sec3-inc02 ul { margin-left: 8%; }
}
@media (max-width: 1300px) {
  #sec3-inc02 { padding: 80px 30px; }
}
@media (max-width: 1024px) {
  #sec3-inc02 { height: 600px; padding-top: 0; }
  .sec3-tit_area p { font-size: 42px; }
  .sec3-tit_area .sec3-pl { font-size: 16px; }
  #sec3-inc02 ul li span.sec3-counting { font-size: 40px; }
  #sec3-inc02 ul li .sec3-tit { font-size: 26px; }
  #sec3-inc02 ul li p i { font-size: 26px; }
}
@media (max-width: 768px) {
  #sec3-inc02 { display: block; padding: 60px 20px; }
  .sec3-tit_area { padding: 0 20px 35px; text-align: center; }
  .sec3-tit_area p { margin-bottom: 10px; font-size: 32px; }
  .sec3-tit_area .sec3-pl { white-space: normal; word-break: keep-all; }
  #sec3-inc02 ul { margin-left: 0; }
  #sec3-inc02 ul li span.sec3-counting { font-size: 32px; }
  #sec3-inc02 ul li .sec3-tit { margin-bottom: 15px; font-size: 22px; }
}
@media (max-width: 480px) {
  #sec3-inc02 { height: auto; }
  #sec3-inc02 ul { display: block; }
  #sec3-inc02 ul li { width: auto; }
  #sec3-inc02 ul li + li { margin-top: 25px; }
  #sec3-inc02 ul li span.sec3-counting { font-size: 26px; }
  #sec3-inc02 ul li p i { font-size: 20px; }
  #sec3-inc02 ul li .sec3-tit { margin-bottom: 10px; font-size: 18px; }
}
