@import url('https://fonts.googleapis.com/css?family=Raleway:400,500,700');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
@font-face {
    font-family: 'KimjungchulMyungjo-Bold';
    src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/KimjungchulMyungjo-Bold.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
}
@font-face {
    font-family: 'Pretendard-Regular';
    src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
    font-weight: 400;
    font-style: normal;
}


body, html {
  height: 100%;
  margin: 0;
  padding: 0;
}
ul, li, dl, dt, dd, p, span { margin: 0; padding: 0; }
a { text-decoration: none; }
li { list-style: none; }
*, :after, :before { box-sizing: border-box; }

.sec4-main {
  overflow: visible;
  background: #111;
  min-height: 100vh;
}

.sec4-hor {
  overflow: visible;
  display: flex;
  flex-wrap: nowrap;
  width: 300vw;
  position: relative;
}

.sec4-hor-section {
  width: 100vw;
  flex-shrink: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #111;
  position: relative;
}

.sec4-imgbox {
  position: relative;
  width: 70vw;
  max-width: 900px;
  aspect-ratio: 16/9;
  background: #222;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  cursor: pointer;
}

/* --- snip1477 스타일 --- */
.snip1477 {
  font-family: 'Raleway', Arial, sans-serif;
  position: relative;
  overflow: hidden;
  background-color: #000;
  color: #fff;
  text-align: center;
  font-size: 16px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.18);
  transition: box-shadow 0.3s;
}

.snip1477 img.sec4-img-real {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.9;
  transition: opacity 0.5s cubic-bezier(.4,0,.2,1);
  display: block;
}

.snip1477.active img.sec4-img-real,
.snip1477:hover img.sec4-img-real {
  opacity: 0.35;
}

.sec4-title-frame {
  position: absolute;
  top: 58%;
  left: 25px;
  padding: 5px 10px 10px;
  z-index: 2;
  pointer-events: none;
}
.sec4-title-frame:before,
.sec4-title-frame:after {
  height: 2px;
  width: 400px;
  position: absolute;
  content: '';
  background-color: #fff;
  transition: transform 0.55s cubic-bezier(.4,0,.2,1);
}
.sec4-title-frame:before {
  top: 0;
  left: 10px;
  transform: translateX(100%);
}
.sec4-title-frame:after {
  bottom: 0;
  right: 10px;
  transform: translateX(-100%);
}
.sec4-title-frame div:before,
.sec4-title-frame div:after {
  width: 2px;
  height: 300px;
  position: absolute;
  content: '';
  background-color: #fff;
  transition: transform 0.55s cubic-bezier(.4,0,.2,1);
}
.sec4-title-frame div:before {
  top: 10px;
  right: 0;
  transform: translateY(100%);
}
.sec4-title-frame div:after {
  bottom: 10px;
  left: 0;
  transform: translateY(-100%);
}
.snip1477.active .sec4-title-frame:before,
.snip1477:hover .sec4-title-frame:before,
.snip1477.active .sec4-title-frame:after,
.snip1477:hover .sec4-title-frame:after,
.snip1477.active .sec4-title-frame div:before,
.snip1477:hover .sec4-title-frame div:before,
.snip1477.active .sec4-title-frame div:after,
.snip1477:hover .sec4-title-frame div:after {
  transform: translate(0, 0);
}

.snip1477 h2,
.snip1477 h4 {
  margin: 0;
  text-transform: uppercase;
}
.snip1477 h2 {
  font-weight: 400;
  font-size: 2.1rem;
  letter-spacing: 0.04em;
}
.snip1477 h4 {
  display: block;
  font-weight: 700;
  background-color: #fff;
  padding: 5px 10px;
  color: #000;
  margin-top: 10px;
  font-size: 1.1rem;
  letter-spacing: 0.04em;
}

/* 설명글 오버레이 */
.sec4-desc-figcaption {
  position: absolute;
  bottom: 42%;
  left: 25px;
  text-align: left;
  opacity: 0;
  padding: 5px 60px 5px 10px;
  font-size: 1.03em;
  font-weight: 500;
  letter-spacing: 1.5px;
  z-index: 3;
  transition: opacity 0.38s cubic-bezier(.4,0,.2,1) 0.2s;
  pointer-events: none;
}
.sec4-desc-figcaption.active {
  opacity: 1;
  pointer-events: auto;
}

.sec4-desc-figcaption p {
  margin: 0 0 10px 0;
  line-height: 1.6;
}

.sec4-credit {
  display: block;
  margin-top: 8px;
  color: #fff;
  font-size: 1rem;
  font-weight: 400;
  opacity: 0.95;
}

.snip1477 a {
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  z-index: 10;
}

/* 반응형 */
@media (max-width: 900px) {
  .sec4-imgbox {
    width: 95vw;
    max-width: 98vw;
  }
  .sec4-title-frame:before,
  .sec4-title-frame:after {
    width: 60vw;
  }
  .sec4-title-frame div:before,
  .sec4-title-frame div:after {
    height: 30vw;
  }
  .sec4-desc-figcaption {
    padding: 5px 12vw 5px 4vw;
    font-size: 0.97em;
  }
}

@media (max-width: 600px) {
  .sec4-imgbox {
    width: 99vw;
    max-width: 99vw;
  }
  .sec4-title-frame:before,
  .sec4-title-frame:after {
    width: 90vw;
  }
  .sec4-title-frame div:before,
  .sec4-title-frame div:after {
    height: 28vw;
  }
  .sec4-desc-figcaption {
    left: 4vw;
    bottom: 34vw;
    padding: 2vw 8vw 2vw 2vw;
    font-size: 0.93em;
  }
}
