.main-container {
  width: 100vw;
  min-height: 100vh;
  background: #111;
  color: #ffffff;
  font-family: 'Termina', sans-serif;
  position: relative;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

/* Top 버튼 */
.top-btn {
  position: fixed;
  right: 36px;
  bottom: 44px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: linear-gradient(135deg, #2196f3 0%, #7de2ff 100%);
  color: #fff;
  border: none;
  box-shadow: 0 4px 18px rgba(33,150,243,0.18);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 2000;
  transition: background 0.18s, box-shadow 0.18s, transform 0.18s;
  opacity: 0.92;
}
.top-btn:hover {
  background: linear-gradient(135deg, #7de2ff 0%, #2196f3 100%);
  box-shadow: 0 6px 24px rgba(33,150,243,0.25);
  transform: translateY(-4px) scale(1.06);
}
.top-btn svg {
  display: block;
}
@media (max-width: 600px) {
  .top-btn {
    right: 14px;
    bottom: 18px;
    width: 42px;
    height: 42px;
  }
}
